import React from "react";
import "./styles.css";
import menu from "./menu";

import MYcheckboxes from "./checkboxes";
import Misekata from "./ToggleButton";
import TextBox from "./TextBox";

function menus(index) {
  const items = [];
  const keys = Object.keys(menu[index]);
  const values = Object.values(menu[index]);

  for (let i = 0; i < values.length; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}

export default function App() {
  return (
    <div className="App">
      <TextBox />
      <ul>
        <li>
          <MYcheckboxes />
          {menus(0)}
        </li>
        <li>
          <MYcheckboxes />
          {menus(1)}
        </li>
        <li>
          <MYcheckboxes />
          {menus(2)}
        </li>
        <li>
          <MYcheckboxes />
          {menus(3)}
        </li>
        <li>
          <MYcheckboxes />
          {menus(4)}
        </li>
      </ul>
    </div>
  );
}
