import React from "react";
import "./styles.css";
import menu from "./menu";

import MYcheckboxes from "./checkboxes";
import Misekata from "./ToggleButton";
import TextBox from "./TextBox";
import Yoko from "./yokomise";
import Tate from "./tatemise";

function menus(index) {
  const items = [];
  const keys = Object.keys(menu[index]);
  const values = Object.values(menu[index]);

  document.getElementById("square-button").onclick = function () {
    this.classList.toggle("blue");
  };

  for (let i = 0; i < values.length; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}

export default function App() {
  return (
    <div className="App">
      <h1>ダイアトニックコード早見表</h1>
      <Misekata />
      <Yoko />
    </div>
  );
}
